<template>
  <view class="food-shop-container" :style="{ paddingTop: navPaddingTop + 'px' }">
    <custom-nav-bar
      title="Á∫ø‰∏äÁæéÈ£üÈÄâË¥≠"
      subtitle="Â£ÆÊóèÁâπËâ≤ÁæéÈ£ü‰∫ßÂìÅ"
      gradient="linear-gradient(120deg, #e65100 0%, #ff8f00 45%, #ffb300 100%)"
    />
    
    <!-- ÂàÜÁ±ªÊ†áÁ≠æÊ†è -->
    <view class="categories">
      <scroll-view scroll-x class="category-scroll" :show-scrollbar="false">
        <view 
          class="category-item"
          :class="{ active: selectedCategory === '' }"
          @click="filterByCategory('')"
        >
          <text>ÂÖ®ÈÉ®</text>
        </view>
        <view 
          v-for="category in categories" 
          :key="category.id"
          class="category-item"
          :class="{ active: selectedCategory === category.id }"
          @click="filterByCategory(category.id)"
        >
          <text>{{ category.icon }} {{ category.name }}</text>
        </view>
      </scroll-view>
    </view>
    
    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <view class="loading" v-if="loading">
      <text>Âä†ËΩΩ‰∏≠...</text>
    </view>
    
    <!-- ‰∫ßÂìÅÂàóË°®ÔºàÁΩëÊ†ºÂ∏ÉÂ±ÄÔºâ -->
    <view class="product-grid" v-else-if="products.length > 0">
      <view 
        v-for="product in products" 
        :key="product.id"
        class="product-card"
        @click="openProductDetail(product)"
      >
        <image 
          :src="product.image" 
          class="product-image" 
          mode="aspectFill"
        ></image>
        <view class="product-info">
          <text class="product-name">{{ product.name }}</text>
          <text class="product-price">¬•{{ product.price }}</text>
        </view>
      </view>
    </view>

    <!-- Á©∫Áä∂ÊÄÅ -->
    <view class="empty-state" v-else>
      <text class="empty-icon">üçú</text>
      <text class="empty-text">ÊöÇÊó†‰∫ßÂìÅ</text>
    </view>

    <!-- ‰∫ßÂìÅËØ¶ÊÉÖÂºπÁ™ó -->
    <view class="modal-overlay" v-if="showDetail" @click="closeProductDetail">
      <view class="product-detail-modal" @click.stop>
        <view class="modal-header">
          <text class="modal-title">‰∫ßÂìÅËØ¶ÊÉÖ</text>
          <text class="modal-close" @click="closeProductDetail">√ó</text>
        </view>
        
        <scroll-view scroll-y class="modal-content" v-if="selectedProduct">
          <image 
            :src="selectedProduct.image" 
            class="detail-image" 
            mode="aspectFill"
          ></image>
          <view class="detail-info">
            <text class="detail-name">{{ selectedProduct.name }}</text>
            <text class="detail-price">¬•{{ selectedProduct.price }}</text>
            <text class="detail-desc">{{ selectedProduct.description }}</text>
            
            <!-- È£üÊùêËØ¥Êòé -->
            <view class="detail-section" v-if="selectedProduct.ingredients">
              <text class="section-label">È£üÊùêÔºö</text>
              <text class="section-text">{{ selectedProduct.ingredients }}</text>
            </view>
            
            <!-- Âè£Âë≥ÁâπÁÇπ -->
            <view class="detail-section taste-section" v-if="selectedProduct.taste">
              <text class="section-label">Âè£Âë≥ÁâπÁÇπÔºö</text>
              <text class="section-text">{{ selectedProduct.taste }}</text>
            </view>
          </view>
        </scroll-view>
        
        <view class="modal-footer">
          <button class="buy-btn" @click="navigateToShop">
            Á´ãÂç≥Ë¥≠‰π∞
          </button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import CustomNavBar from '@/components/CustomNavBar.vue'
import { getFoodCategories, getFoodProducts, getFoodShopUrl } from '@/services/food-product.service.js'

export default {
  components: { CustomNavBar },
  data() {
    return {
      statusBarHeight: 20,
      navPaddingTop: 140,
      categories: [],
      products: [],
      selectedCategory: '',
      selectedProduct: null,
      showDetail: false,
      loading: true
    }
  },
  onLoad() {
    const info = uni.getSystemInfoSync()
    const sbh = info.statusBarHeight || 20
    this.statusBarHeight = sbh
    this.navPaddingTop = sbh + 75
    this.loadProducts()
  },
  methods: {
    /**
     * Âä†ËΩΩ‰∫ßÂìÅÊï∞ÊçÆ
     */
    async loadProducts() {
      this.loading = true
      try {
        this.categories = getFoodCategories()
        this.products = getFoodProducts(this.selectedCategory)
      } catch (err) {
        console.error('Failed to load products:', err)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },

    /**
     * ÊåâÂàÜÁ±ªÁ≠õÈÄâ‰∫ßÂìÅ
     * @param {string} categoryId - ÂàÜÁ±ªID
     */
    filterByCategory(categoryId) {
      this.selectedCategory = categoryId
      this.products = getFoodProducts(categoryId)
    },
    
    /**
     * ÊâìÂºÄ‰∫ßÂìÅËØ¶ÊÉÖÂºπÁ™ó
     * @param {Object} product - ‰∫ßÂìÅÂØπË±°
     */
    openProductDetail(product) {
      this.selectedProduct = product
      this.showDetail = true
    },
    
    /**
     * ÂÖ≥Èó≠‰∫ßÂìÅËØ¶ÊÉÖÂºπÁ™ó
     */
    closeProductDetail() {
      this.showDetail = false
      this.selectedProduct = null
    },
    
    /**
     * Ë∑≥ËΩ¨Â§ñÈÉ®ÂïÜÂüé
     */
    navigateToShop() {
      if (!this.selectedProduct) {
        return
      }
      
      const shopUrl = getFoodShopUrl(this.selectedProduct.id)
      
      if (!shopUrl) {
        uni.showToast({
          title: 'ÂïÜÂüéÈìæÊé•ÊöÇ‰∏çÂèØÁî®',
          icon: 'none'
        })
        return
      }
      
      // Â∞ùËØïË∑≥ËΩ¨Â§ñÈÉ®ÈìæÊé•
      // #ifdef H5
      window.open(shopUrl, '_blank')
      // #endif
      
      // #ifndef H5
      uni.navigateTo({
        url: `/pages/webview/webview?url=${encodeURIComponent(shopUrl)}`,
        fail: () => {
          // Â¶ÇÊûúÊ≤°ÊúâwebviewÈ°µÈù¢ÔºåÂ∞ùËØïÂ§çÂà∂ÈìæÊé•
          uni.setClipboardData({
            data: shopUrl,
            success: () => {
              uni.showToast({
                title: 'ÈìæÊé•Â∑≤Â§çÂà∂ÔºåËØ∑Âú®ÊµèËßàÂô®‰∏≠ÊâìÂºÄ',
                icon: 'none',
                duration: 2000
              })
            },
            fail: () => {
              uni.showToast({
                title: 'ÂïÜÂüéÈìæÊé•ÊöÇ‰∏çÂèØÁî®',
                icon: 'none'
              })
            }
          })
        }
      })
      // #endif
    }
  }
}
</script>

<style scoped>
.food-shop-container {
  min-height: 100vh;
  background: #f5f5f5;
  padding-bottom: 40rpx;
}

/* ÂàÜÁ±ªÊ†áÁ≠æÊ†è */
.categories {
  background: #ffffff;
  padding: 20rpx 0;
  margin-bottom: 20rpx;
}

.category-scroll {
  white-space: nowrap;
  padding: 0 20rpx;
}

.category-item {
  display: inline-block;
  padding: 16rpx 32rpx;
  margin-right: 20rpx;
  border-radius: 30rpx;
  background: #f0f0f0;
  font-size: 28rpx;
  color: #666;
}

.category-item.active {
  background: #ff8f00;
  color: #ffffff;
}

/* Âä†ËΩΩÁä∂ÊÄÅ */
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100rpx 0;
  color: #999;
  font-size: 28rpx;
}

/* ‰∫ßÂìÅÁΩëÊ†º */
.product-grid {
  display: flex;
  flex-wrap: wrap;
  padding: 0 20rpx;
  gap: 20rpx;
}

.product-card {
  width: calc(50% - 10rpx);
  background: #ffffff;
  border-radius: 16rpx;
  overflow: hidden;
  box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.06);
}

.product-image {
  width: 100%;
  height: 320rpx;
  background: #f0f0f0;
}

.product-info {
  padding: 20rpx;
}

.product-name {
  display: block;
  font-size: 28rpx;
  font-weight: 600;
  color: #333;
  margin-bottom: 10rpx;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.product-price {
  display: block;
  font-size: 32rpx;
  font-weight: bold;
  color: #ff8f00;
}

/* Á©∫Áä∂ÊÄÅ */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 100rpx 0;
}

.empty-icon {
  font-size: 80rpx;
  margin-bottom: 20rpx;
}

.empty-text {
  font-size: 28rpx;
  color: #999;
}

/* ÂºπÁ™óÈÅÆÁΩ© */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

/* ‰∫ßÂìÅËØ¶ÊÉÖÂºπÁ™ó */
.product-detail-modal {
  width: 90%;
  max-width: 600rpx;
  background: #ffffff;
  border-radius: 20rpx;
  overflow: hidden;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30rpx;
  border-bottom: 1rpx solid #eee;
}

.modal-title {
  font-size: 32rpx;
  font-weight: 600;
  color: #333;
}

.modal-close {
  font-size: 48rpx;
  color: #999;
  line-height: 1;
}

.modal-content {
  flex: 1;
  padding: 30rpx;
  max-height: 60vh;
}

.detail-image {
  width: 100%;
  height: 400rpx;
  border-radius: 12rpx;
  background: #f0f0f0;
  margin-bottom: 30rpx;
}

.detail-info {
  display: flex;
  flex-direction: column;
}

.detail-name {
  font-size: 34rpx;
  font-weight: 600;
  color: #333;
  margin-bottom: 16rpx;
}

.detail-price {
  font-size: 40rpx;
  font-weight: bold;
  color: #ff8f00;
  margin-bottom: 20rpx;
}

.detail-desc {
  font-size: 28rpx;
  color: #666;
  line-height: 1.6;
  margin-bottom: 20rpx;
}

/* ËØ¶ÊÉÖÂå∫Âùó */
.detail-section {
  background: #fff8e1;
  border-radius: 12rpx;
  padding: 20rpx;
  margin-top: 10rpx;
}

.taste-section {
  background: #fff3e0;
}

.section-label {
  font-size: 26rpx;
  font-weight: 600;
  color: #ff8f00;
}

.section-text {
  font-size: 26rpx;
  color: #666;
  line-height: 1.6;
}

.modal-footer {
  padding: 30rpx;
  border-top: 1rpx solid #eee;
}

.buy-btn {
  width: 100%;
  background: linear-gradient(135deg, #ff8f00 0%, #ffb74d 100%);
  color: #ffffff;
  font-size: 32rpx;
  font-weight: 600;
  border-radius: 40rpx;
  padding: 24rpx 0;
  border: none;
}

.buy-btn:active {
  opacity: 0.9;
}
</style>
